/* Estilos gerais */
.container {
    background-image: url('data:image/jpeg;base64,'); /* Caminho para a imagem de fundo */
    background-size: cover; /* Ajusta a imagem para cobrir toda a área */
    background-position: center; /* Centraliza a imagem */
    background-repeat: no-repeat; /* Impede a repetição da imagem */
    min-height: 100vh; /* Garante que o fundo cubra toda a altura da janela de visualização */
    padding: 20px; /* Adiciona algum espaçamento interno, se necessário */
    display: flex; /* Para centralizar os itens */
    justify-content: center; /* Centraliza horizontalmente */
    align-items: center; /* Centraliza verticalmente */ 
}
h1 {
    margin: 0;
    text-align: left;
    margin-bottom: 10px;
    color: #fff;
}
.login-form, .form-tarefa {
    padding: 5px;
}
.form-field-login, .form-field-tarefa {
    display: inline-block;
    padding: 5px;
    margin: 10px;
    width: calc(100% - 22px); /* Ajustado para os campos de formulário */
}
.form-error-login {
    display: block;
    color: rgb(237, 67, 55);
    font-size: 0.9em;
    font-weight: 600;
}
.botao-login {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 5px 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.botao-login:hover {
    background-color: #0056b3;
}
.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(12, 149, 187, 0.808);
    padding: 20px;
    border-radius: 10px;
    width: 90%; /* Ajusta a largura para responsividade */
    max-width: 400px; /* Define uma largura máxima */
}
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
}
/* Estilos específicos para o gerenciamento de tarefas */
.gerenciamento-tarefas {
    margin: auto;
    width: 90%;
    max-width: 1200px; /* Ajustado para um tamanho máximo mais razoável */
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 7px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.form-tarefa {
    margin-bottom: 20px;
    padding: 15px;
    background-color: #ffffff;
    border-radius: 7px;
}
.form-group-tarefa {
    margin-bottom: 15px;
}
.form-field-tarefa {
    width: calc(100% - 22px); /* Ajustado para corresponder ao layout dos campos de formulário */
    padding: 10px;
    margin-bottom: 10px;
}
.form-field-tarefa-descricao{
    width: calc(100% - 22px); /* Ajustado para corresponder ao layout dos campos de formulário */
    padding: 20px;
    margin-bottom: 10px;
}
.botao-tarefa {
    margin-top: 15px;
    margin-bottom: 15px;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.botao-tarefa:hover {
    background-color: #0056b3;
}
/*lista de tarefas --------------------------------*/
.lista-tarefas {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Cria uma grade responsiva com colunas mínimas de 300px */
    gap: 20px; /* Espaço entre os itens */
    padding: 20px;
}
.lista-tarefas ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
}
.lista-tarefas li {
    background-color: #ffffff;
    border-radius: 7px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 15px;
    box-sizing: border-box; /* Inclui padding e border na largura e altura do elemento */
}
.lista-tarefas h3 {
    margin-top: 0;
}
.botao-tarefa {
    display: block;
    margin-top: 10px;
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.botao-tarefa:hover {
    background-color: #0056b3;
}
  